* {
  padding: 0;
  margin: 0;
  outline: 0;
  box-sizing: border-box;
  color: #fefefe;
}

h1,
h2,
h3 {
  font-weight: 500;
  text-align: center;
}

input{
  width: 80%
}

.hero {
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(#352FEC, #3E2BA5);
}

.heroImage {
  background-image: url('./images/mobile-wallpaper.png');
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.col {
  display: flex;
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.button {
  margin: 15px;
  text-decoration: none;
  padding: 15px 50px;
  font-size: 1.2em;
  border-radius: 60px;
  background-color: #1DAC7F;
  box-shadow: #00FF33 0px 0px 5px;
  transition: 0.2s all;
  border: none;
}

.justify-right-desktop{
  justify-content: center;
}

.button:hover{
  background-color: rgb(28, 146, 109);
  color: #fff;
  box-shadow: none
}
.purple-text{
  color: #5241AC;
}
.purple{
  background-color: #5241AC;
}

.light-purple-text{
  color: #776DAB;
}

@media screen and (min-width: 1023px){
  .desktop-row{
    flex-direction: row;
    justify-content: space-evenly;
  }

  .desktop-none{
    display: none;
  }
  .heroImage{
    background-image: url('./images/wallpaper.svg');
    padding: 20px;
  }
  .justify-right-desktop{
    align-items: flex-end;
  }

  input{
    width: 50%;
  }
}